export default {
    props: {
        title: String,
        main: Boolean,
        preventGoBack: {
          type: Boolean,
          default: false
        },
        showLeft: {
          type: Boolean,
          default: true
        },
        showRight: {
          type: Boolean,
          default: true
        },
      getPlaceHolder: String,
        leftText: String,
        leftAction: Function,
        rightText: String,
        rightAction: Function,
        mainTitle: String,
        subTitle: String,
        search: {
          type: Boolean,
          default: false
        },
        searchSubmit: Function,
        searchFocus:{
          type:Function,
          default:()=>{},
        },
        searchChange: Function
    },
    data () {
      return {
        searchText: ''
      }
    },
    methods: {
      //返回
      goBack(){
        this.$router.go(-1);
      },
      cancel(){
        this.$router.go(-1);
      }
    }
}



